:host(limel-lime-ai-avatar) {
    display: flex;
    justify-content: center;
    align-self: center;
    position: relative;
    isolation: isolate;

    aspect-ratio: 1;
    max-width: 10rem;
    max-height: 10rem;
    min-width: 1.75rem;
    min-height: 1.75rem;
}

* {
    box-sizing: border-box;
}

svg {
    position: absolute;
    margin: auto;
    mix-blend-mode: screen;

    animation-iteration-count: infinite;
    animation-play-state: var(--lime-ai-avatar-animation-play-state, paused);
}

.red {
    rotate: 74deg;
    color: rgb(var(--color-red-default));
    animation-name: animate-red;
    animation-duration: 6s;
}

.green {
    color: rgb(var(--color-green-default));
    animation-name: animate-green;
    animation-duration: 5.5s;
}

.blue {
    rotate: 100deg;
    color: rgb(var(--color-blue-default));
    animation-name: animate-blue;
    animation-duration: 5s;
}

.orange {
    color: rgb(var(--color-orange-default));
    animation-name: animate-yellow;
    animation-duration: 6.5s;
}

@keyframes animate-red {
    0% {
        transform: rotate(0deg) scaleX(1) scaleY(0.7);
    }
    25% {
        transform: rotate(90deg) scaleX(0.8) scaleY(0.9);
    }
    50% {
        transform: rotate(180deg) scaleX(1) scaleY(0.6);
    }
    75% {
        transform: rotate(270deg) scaleX(0.8) scaleY(0.9);
    }
    100% {
        transform: rotate(360deg) scaleX(1) scaleY(0.7);
    }
}

@keyframes animate-green {
    0% {
        transform: rotate(31deg) scaleX(0.7) scaleY(1);
    }
    25% {
        transform: rotate(121deg) scaleX(0.9) scaleY(0.7);
    }
    50% {
        transform: rotate(211deg) scaleX(0.7) scaleY(1);
    }
    75% {
        transform: rotate(301deg) scaleX(0.9) scaleY(0.7);
    }
    100% {
        transform: rotate(391deg) scaleX(0.7) scaleY(1);
    }
}

@keyframes animate-blue {
    0% {
        transform: rotate(413deg) scaleX(0.9) scaleY(0.7);
    }
    50% {
        transform: rotate(233deg) scaleX(0.7) scaleY(0.9);
    }
    100% {
        transform: rotate(53deg) scaleX(0.9) scaleY(0.7);
    }
}

@keyframes animate-yellow {
    0% {
        transform: rotate(472deg) scaleX(0.7) scaleY(0.95);
    }
    25% {
        transform: rotate(382deg) scaleX(0.95) scaleY(0.7);
    }
    50% {
        transform: rotate(292deg) scaleX(0.7) scaleY(0.95);
    }
    75% {
        transform: rotate(202deg) scaleX(0.95) scaleY(0.7);
    }
    100% {
        transform: rotate(112deg) scaleX(0.7) scaleY(0.95);
    }
}
